<template>
  <div class="map">
    <el-tag
      v-for="(item, index) in arrypath"
      :key="item.name"
      :closable="item.name !== 'home'"
      :effect="$route.name === item.name ? 'dark' : 'plain'"
      @click="changetab(item)"
      @close="deltab(item, index)"
    >
      {{ item.title }}
    </el-tag>
  </div>
</template>
<script>
export default {
  data() {
    return {}
  },
  computed: {
    //获取vuex共享的可移除标签name
    arrypath() {
      return this.$store.state.tab.arrypath
    },
  },
  methods: {
    //可移除标签间切换
    changetab(item) {
      this.$router.push(item.path)
    },
    //移除可移除标签
    deltab(item, index) {
      this.$store.commit('deltab', item)
      const length = this.arrypath.length
      //判断移除的标签是否是当前所在页面标签
      if (item.path !== this.$route.path) {
        return
      }
      if (index === length) {
        this.$router.push({ name: this.arrypath[index - 1].name })
      } else {
        this.$router.push({ name: this.arrypath[index].name })
      }
    },
  },
}
</script>
<style lang="less" scoped>
.map {
  display: flex;
  flex-flow: nowrap;
}
.el-tag {
  margin: 6px;
}
</style>