<template>
  <el-menu
    show-timeout="800"
    hide-timeout="800"
    default-active="/home"
    background-color="#4c4c4c"
    active-text-color="#FFF68F"
    class="el-menu-vertical-demo"
    text-color="aliceblue"
    :collapse="isCollapse"
  >
    <h3 v-text="isCollapse ? '后台' : '后台管理系统'">后台管理系统</h3>
    <el-menu-item
      @click="tzhome(item)"
      v-for="item in arryone"
      :index="item.path + ''"
      :key="item.path"
    >
      <i :class="item.icon"></i>
      <span slot="title">{{ item.title }}</span>
    </el-menu-item>
    <el-submenu v-for="item in arry" :index="item.path + ''" :key="item.path">
      <template slot="title">
        <i :class="item.icon"></i>
        <span slot="title">{{ item.title }}</span>
      </template>
      <el-menu-item-group v-for="items in arry[0].children" :key="items.path">
        <el-menu-item @click="tzpage(items)" :index="items.path + ''"
          ><i :class="items.icon"></i>{{ items.title }}</el-menu-item
        >
      </el-menu-item-group>
    </el-submenu>
  </el-menu>
</template>
<script>
export default {
  data() {
    return {
      isshow: '后台管理系统',
      arryone: [
        {
          path: '/home',
          name: 'home',
          title: '首页',
          icon: 'el-icon-house',
          url: '',
        },
        {
          path: '/foods',
          name: 'foods',
          title: '商品管理',
          icon: 'el-icon-data-analysis',
          url: '',
        },
        {
          path: '/me',
          name: 'me',
          title: '用户管理',
          icon: 'el-icon-user',
          url: '',
        },
      ],
      arry: [
        {
          title: '其他',
          icon: 'el-icon-more',
          children: [
            {
              path: '/pageone',
              name: 'pageone',
              title: '页面一',
              icon: 'el-icon-document-remove',
              url: '',
            },
            {
              path: '/pagetwo',
              name: 'pagetwo',
              title: '页面二',
              icon: 'el-icon-document-remove',
              url: '',
            },
          ],
        },
      ],
    }
  },
  methods: {
    tzhome(item) {
      //左侧栏目跳转
      const e = item.path
      //方法一： if(this.$route.path!==e&&!(this.$route==='/home'&&e==='/')){
      //   this.$router.push(e)
      // }
      this.$router.push(e).catch((err) => {
        console.log()
      }),
        this.$store.commit('clickpath', item)
    },
    //其他页面下的page页面跳转
    tzpage(items) {
      const e = items.path
      this.$router.push(e).catch((err) => {
        console.log()
      })
    },
  },
  computed: {
    //获得vuex共享的展开收起状态
    isCollapse() {
      return this.$store.state.tab.isCollapse
    },
  },
}
</script>
<style lang="less" scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 420px;
  text-align: left;
}
.el-menu {
  border-right: 0px;
}
.el-menu {
  height: 98vh;
  h3 {
    text-align: center;
    color: aliceblue;
    font-size: 14px;
    font-weight: 500;
  }
}
</style>