<template>
  <div class="map">
    <div class="lefticon">
      <el-button @click="changeer" plain icon="el-icon-menu"></el-button>
      <!-- 面包屑 -->
      <h6>
        <el-breadcrumb separator="/">
          <el-breadcrumb-item
            v-for="item in tags"
            :key="item.name"
            :to="{ path: item.path }"
            >{{ item.title }}</el-breadcrumb-item
          >
        </el-breadcrumb>
      </h6>
    </div>
    <div>
      <el-dropdown @command="handleCommand">
        <img
          src="../assets/img/avatar.jpg"
          class="el-icon-arrow-down el-icon--right meicon"
        />
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>个人中心</el-dropdown-item>
          <el-dropdown-item command="loginout">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>
<script>
import Cookie from 'js-cookie'
export default {
  data() {
    return {
      col: true,
    }
  },
  methods: {
    handleClick() {
      alert('button click')
    },
    changeer() {
      // bus.$emit('colle',this.col)
      // this.col=!this.col
      //控制缩进与展开按钮
      this.$store.commit('isCollapseMun')
    },
    //登出清除token
    handleCommand(command) {
      if (command === 'loginout') {
        Cookie.remove('token')
        this.$router.push('/login')
      }
    },
  },
  computed: {
    tags() {
      return this.$store.state.tab.arrypath
    },
    //方法二:
    // ...mapState({
    //   tags: (state) => state.tab.arrypath,
    // }),
  },
}
</script>
<style lang="less" scoped>
.map {
  display: flex;
  flex-flow: row;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
/deep/.el-menu {
  border-right: 0px;
}
.lefticon {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-start;
  justify-content: flex-start;
  align-items: center;
  h6 {
    margin-left: 18px;
    /deep/ .is-link {
      color: rgb(240, 238, 238);
    }
    .el-breadcrumb {
      display: flex;
      flex-flow: nowrap;
    }
  }
}
.el-dropdown {
  vertical-align: top;
}
.el-dropdown + .el-dropdown {
  margin-left: 15px;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.meicon {
  border-radius: 50%;
  width: 6vh;
  height: 6vh;
}
</style>