export default {
    state: {
        //控制左侧缩进与展开属性
        isCollapse: false,
        op: 'oppop',
        //标签对应页面信息
        arrypath: [
            {
                path: '/home',
                name: 'home',
                title: '首页',
                icon: 'el-icon-house',
                url: ''
            }
        ]
    },
    mutations: {
        //左侧栏缩进与展开
        isCollapseMun(state) {
            state.isCollapse = !state.isCollapse
        },
        //面包屑添加
        clickpath(state, val) {
            if (val.name !== 'home') {
                const index = state.arrypath.findIndex((item) => item.name === val.name)
                if (index === -1) {
                    state.arrypath.push(val)
                }
            }
        },
        //面包屑删除
        deltab(state, val) {
            const index = state.arrypath.findIndex((item) => item.path === val.path)
            state.arrypath.splice(index, 1)
        }
    }
}