import Mock from "mockjs";
const { arry } = Mock.mock({
  'arry|64': [
    {
      'id|+1': 1,
      'gender|1': true,
      name: '@cname()',
      'age|20-50': 25,
      dates: '@date()',
      area: '@city(true)'
    }
  ]
}
)
//获取数据
const getqu = (url, name) => {
  const index = url.indexOf('?')
  if (index !== -1) {
    const quarr = url.substr(index + 1).split('&')
    for (var i = 0; i < quarr.length; i++) {
      const itemarr = quarr[i].split('=')
      if (itemarr[0] === name) {
        return itemarr[1]
      }
    }
  }
  return null
}
Mock.mock(/\/api\/home\/getdata/, 'get', function (options) {
  const pageindex = getqu(options.url, 'pageindex')
  const pagesize = getqu(options.url, 'pagesize')
  const start = (pageindex - 1) * pagesize
  const end = pageindex * pagesize
  const totalPag = Math.ceil(arry.length / pagesize)
  const arrys = pageindex > totalPag ? [] : arry.slice(start, end)
  return {
    status: 200,
    msg: '获取数据成功',
    arry: arrys,
    total: arry.length
  }
})
//添加数据
Mock.mock('/api/home/adddata', 'post', (options) => {
  const body = JSON.parse(options.body)
  console.log(body);
  console.log(options);
  arry.unshift(Mock.mock({
    id: body.id,
    gender: body.gender,
    name: body.name,
    age: body.age,
    dates: body.dates,
    area: body.area
  }))
  return {
    arry: arry
  }
})
//删除数据
Mock.mock('/api/home/deldata', 'post', (options) => {
  const val = JSON.parse(options.body)
  console.log(val.row);
  const index = arry.findIndex(item => item.id === val.row.id)
  if (index !== -1) {
    arry.splice(index, 1)
  }
  return {
    arry: arry
  }
})
//编辑
Mock.mock('/api/home/changdata', 'post', (options) => {
  const val = JSON.parse(options.body)
  console.log(val);
  const index = arry.findIndex(item => item.id === val.forms.id
  );
  if (index !== -1) {
    arry.splice(index, 1, {
      ...JSON.parse(JSON.stringify(val.forms)),
    });
  }
})
//搜索
Mock.mock('/api/home/search', 'post', (options) => {
  const val = JSON.parse(options.body).name
  const a = arry.filter(item => item.name.indexOf(val) !== -1)
  return a;
})
