//登录token
Mock.mock('/api/home/token', 'post', (options) => {
  const token = Mock.Random.guid()
  console.log(options);
  const user = JSON.parse(options.body)
  console.log(user);
  if (user.username === 'root' && user.password === '123456') {
    return {
      code: 200,
      token: token
    }
  } else {
    return {
      msg: '用户名或密码错误！'
    }
  }
})