<template>
  <div class="map">
    <!-- 弹出的表单 -->
    <el-dialog
      :title="tits"
      :visible.sync="dialogVisible"
      :before-close="handleClose"
      width="30%"
    >
      <el-form
        :rules="rules"
        ref="forms"
        :inline="true"
        :model="formInline"
        class="demo-form-inline"
      >
        <el-form-item label="姓名" prop="name">
          <el-input
            v-model="formInline.name"
            placeholder="请输入姓名"
          ></el-input>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input
            v-model="formInline.age"
            placeholder="请输入年龄"
          ></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="gender">
          <el-select v-model="formInline.gender" placeholder="请选择性别">
            <el-option label="男" :value="true"></el-option>
            <el-option label="女" :value="false"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="出生日期" prop="dates">
          <el-date-picker
            type="date"
            placeholder="请选择出生日期"
            value-format="yyyy-MM-dd"
            v-model="formInline.dates"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="地址" prop="area">
          <el-input
            v-model="formInline.area"
            placeholder="请输入地址"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closedia">取 消</el-button>
        <el-button type="primary" @click="subm">确 定</el-button>
      </span>
    </el-dialog>
    <div class="tables">
      <!-- 新增与搜索 -->
      <div class="input">
        <el-button type="primary" @click="adddata" style="margin-bottom: 8px"
          >+新增</el-button
        >
        <div>
          <el-input
            type="text"
            clearable
            @clear="apis"
            v-model="inputdata"
            prefix-icon="el-icon-search"
            placeholder="请输入姓名"
            style="width: 240px"
          />
          <el-button
            type="primary"
            @click="search"
            style="margin-bottom: 8px; margin-left: 6px"
            >搜索</el-button
          >
        </div>
      </div>
      <!-- 用信息表格 -->
      <el-table :data="arrydata" stripe style="width: 100%">
        <el-table-column prop="name" label="姓名" align="center" width="180">
        </el-table-column>
        <el-table-column prop="age" label="年龄" align="center" width="180">
        </el-table-column>
        <el-table-column prop="gender" label="性别" align="center" width="180">
          <template slot-scope="scope">
            <span>{{ scope.row.gender === true ? '男' : '女' }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="dates"
          label="出生日期"
          align="center"
          width="180"
        >
        </el-table-column>
        <el-table-column prop="area" label="地址" align="center" width="280">
        </el-table-column>
        <el-table-column prop="area" label="操作" align="center" width="180">
          <template slot-scope="scope">
            <el-button size="mini" @click="edito(scope.row)">编辑</el-button>
            <el-button type="danger" size="mini" @click="deldata(scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="80"
      @next-click="nextpage"
      :pager-count="9"
      :page-count="8"
      @prev-click="uppage"
      @current-change="changepage"
      class="pages"
    >
    </el-pagination>
  </div>
</template>
<script>
import { Api } from '@/kapi/api'
import { Mock } from 'mockjs'
import request from '@/utils/request.js'
export default {
  data() {
    return {
      // gen: "性别",
      dialogVisible: false,
      formInline: {
        id: '',
        name: '',
        gender: '',
        area: '',
        age: '',
        dates: '',
      },
      rules: {
        name: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
          { min: 2, max: 6, message: '长度2-6个字符', trigger: 'blur' },
        ],
        age: [{ required: true, message: '请输入年龄', trigger: 'blur' }],
        gender: [{ required: true, message: '请选择性别', trigger: 'change' }],
        dates: [
          { required: true, message: '请选择出生日期', trigger: 'change' },
        ],
        area: [{ required: true, message: '请输入地址', trigger: 'blur' }],
      },
      arrydata: [],
      isOff: 0,
      tits: '',
      inputdata: '',
      pageindex: 1,
    }
  },
  created() {
    this.apis(this.pageindex)
  },
  methods: {
    // async apis() {
    //   const { data: res } = await Api(this.pageindex);
    //    this.arrydata = res.arry;
    //   console.log(res.arry);
    async apis() {
      //请求表单数据，传值当前所处页面数，总页面数
      const { data: res } = await request.get('/home/getdata', {
        params: {
          pageindex: this.pageindex,
          pagesize: 8,
        },
      })
      this.arrydata = res.arry
    },
    //下一页
    nextpage() {
      this.pageindex++
      this.apis()
    },
    //上一页
    uppage() {
      this.pageindex--
      this.apis()
    },
    //点击跳转页
    changepage(val) {
      // this.pageindex
      this.pageindex = val
      this.apis()
    },
    //关闭表单，“X”图标控制
    handleClose() {
      this.dialogVisible = false
      this.$refs.forms.resetFields()
      this.formInline = {}
    },
    //表单中取消按钮
    closedia() {
      this.handleClose()
    },
    onSubmit() {
      // console.log("submit!");
    },
    //表单确定提交
    subm() {
      //表单校验后执行
      this.$refs.forms.validate((val) => {
        //val表示表单校验成功与否
        if (val) {
          //isOff代表新增或编辑表单
          if (this.isOff === 0) {
            // console.log(this.formInline);
            // this.arrydata.push({ ...this.formInline });
            //新增用户
            request
              .post('/home/adddata', {
                name: this.formInline.name,
                gender: this.formInline.gender,
                age: this.formInline.age,
                dates: this.formInline.dates,
                area: this.formInline.area,
              })
              .then((res) => {
                this.apis()
              })
          }
          if (this.isOff === 1) {
            //编辑表单，当前行数据填充到弹出的表单
            request
              .post('/home/changdata', {
                forms: this.formInline,
              })
              .then((res) => {
                this.apis()
              })
          }
          this.dialogVisible = false
          this.handleClose()
        } else {
          //打开表单
          this.dialogVisible = true
        }
      })
    },
    //新增用户信息
    adddata() {
      this.isOff = 0
      this.dialogVisible = true
      this.tits = '新增用户信息'
    },
    //编辑用户信息
    edito(row) {
      this.isOff = 1
      this.dialogVisible = true
      this.tits = '编辑'
      //拷贝当前行数据到弹出的表单
      this.formInline = JSON.parse(JSON.stringify(row))
      //   console.log(this.formInline)
    },
    //弹出删除确认提示
    deldata(row) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          request
            .post('/home/deldata', {
              row: row,
            })
            .then((res) => {
              this.apis()
            })
          this.$message({
            type: 'success',
            message: '删除成功!',
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
          })
        })
    },
    //搜索
    search() {
      request
        .post('/home/search', {
          name: this.inputdata,
        })
        .then((res) => {
          this.arrydata = res.data
        })
    },
  },
}
</script>
<style lang="less" scoped>
.pages {
  margin-top: 8px;
  margin-left: 56vw;
}
.input {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
</style>