<template>
  <div class="map">
    <el-row :gutter="6">
      <el-col :span="9">
        <el-card class="box-card">
          <div class="tx">
            <img
              src="../assets/img/avatar.jpg"
              style="width: 10vh; height: 10vh"
            />
            <h3>
              Admin
              <h5>会员</h5>
            </h3>
          </div>
          <div class="map-b" style="width: auto">
            <p>上次登录时间：<span>2022-9-28</span></p>
            <p>上次登录地点：<span>武汉</span></p>
          </div>
        </el-card>
        <!-- 数据表格 -->
        <el-card style="margin-top: 12px; height: 48vh">
          <el-table :data="tableData" style="width: 100%">
            <el-table-column prop="name" label="商品" width="130">
            </el-table-column>
            <el-table-column prop="price" label="销量" width="130">
            </el-table-column>
            <el-table-column prop="num" label="盈利" width="130">
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
      <!-- 支付记录标签 -->
      <el-col :span="15">
        <div
          style="
            display: flex;
            flex-flow: row wrap;
            justify-content: space-between;
          "
        >
          <el-card
            style="
              width: 15vw;
              min-width: 150px;
              margin: 6px;
              margin-top: 0px;
              display: flex;
            "
            :body-style="{ display: 'flex', padding: 0 }"
            v-for="item in payarry"
            :key="item.id"
          >
            <i
              :class="item.icon"
              class="icon"
              :style="{ backgroundColor: item.color }"
            ></i>
            <div class="mapicon">
              <h3>￥{{ item.price }}</h3>
              <h6>已支付</h6>
            </div>
          </el-card>
        </div>
      </el-col>
      <!-- echarts可视化表 -->
      <el-col :span="15">
        <div>
          <el-card style="height: 31vh; margin: 6px">
            <div ref="mains" style="width: auto; height: 31vh"></div>
          </el-card>
          <div style="display: flex; flex-flow: row nowrap">
            <el-card style="width: 53%; height: 32vh; margin: 6px">
              <div ref="rights" style="width: auto; height: 32vh"></div>
            </el-card>
            <el-card style="width: 47%; height: 32vh; margin: 6px">
              <div ref="lefts" style="width: auto; height: 26vh"></div>
            </el-card>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import * as echarts from 'echarts'
import { tableData, payarry } from '@/kapi/api'
export default {
  name: 'home',
  data() {
    return {
      tableData: [],
      payarry: [],
    }
  },
  created() {},
  methods: {
    //表格数据请求
    async apitableData() {
      const { data: res } = await tableData()
      this.tableData = res.data
      this.showers()
      this.showeri()
      this.showele()
    },
    //支付标签数据请求
    async apipayarry() {
      const { data: res } = await payarry()
      this.payarry = res.data
    },
    showers() {
      const name = []
      const price = []
      this.tableData.forEach((val) => {
        name.push(val.name)
        price.push(val.price)
      })
      var myChart = echarts.init(this.$refs.mains)
      // 指定图表的配置项和数据
      var option = {
        title: {
          text: 'ECharts',
        },
        tooltip: {},
        legend: {
          data: ['销量'],
        },
        xAxis: {
          data: name,
        },
        yAxis: {},
        series: [
          {
            name: '销量',
            type: 'line',
            data: price,
          },
        ],
      }
      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option)
    },
    showeri() {
      const name = []
      const price = []
      this.tableData.forEach((val) => {
        name.push(val.name)
        price.push(val.price)
      })
      var myChart = echarts.init(this.$refs.rights)
      // 指定图表的配置项和数据
      var option = {
        title: {
          text: 'ECharts',
        },
        tooltip: {},
        legend: {
          data: ['销量'],
        },
        xAxis: {
          data: name,
        },
        yAxis: {},
        series: [
          {
            name: '销量',
            type: 'bar',
            data: price,
          },
        ],
      }
      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option)
    },
    showele() {
      const name = []
      const price = []
      const data = []
      this.tableData.forEach((val) => {
        name.push(val.name)
        price.push(val.price)
        data.push({ value: val.price, name: val.name })
      })
      var myChart = echarts.init(this.$refs.lefts)
      // 指定图表的配置项和数据
      var option = (option = {
        legend: {
          orient: 'vertical',
          left: 'left',
          data: name,
        },
        series: [
          {
            type: 'pie',
            data: data,
          },
        ],
      })
      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option)
    },
  },
  mounted() {
    this.apitableData()
    this.apipayarry()
  },
}
</script>
<style lang="less" scoped>
.icon {
  width: 56px;
  height: 100%;
  text-align: center;
  line-height: 40px;
}
.tx {
  display: flex;
  flex-flow: row nowrap;
  justify-content: left;
  align-items: center;
  border-bottom: 1px solid rgb(73, 73, 73);
  img {
    border-radius: 50%;
  }
  h3 {
    margin-left: 20px;
    line-height: 12px;
  }
}
.mapicon {
  display: flex;
  flex-flow: column nowrap;
  padding-left: 12px;
  justify-content: space-around;
  height: 50px;
  h3 {
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 2px;
  }
  h6 {
    font-size: 8px;
    margin-top: 2px;
    margin-left: 2px;
  }
}
.map-b {
  text-align: left;
  display: flex;
  flex-flow: column nowrap;
  line-height: 1vh;
}
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
</style>